body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.ContainWrap {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 100vh;
    width: 100vw;
    background: url(/carsend/static/media/Login.957427b8.jpg);
    background-size: cover;
}

.Wrap {
    position: relative;
    width: 400px;
    background: rgba(34, 34, 34, 0.836);
    border-radius: 6px;
    margin-right: 200px;
    padding: 10px;
    text-align: center;
}

.logo {
    height: 60px;
}

form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

input {
    height: 40px;
    width: 90%;
    border: 1px solid rgb(124, 124, 124);
    border-radius: 4px;
    margin: 5px 0;
    padding: 0 5px;
    font-size: 18px;
}

.enviar {
    width: 100px;
    height: 40px;
    background: #cf281e;
    color: #fff;
    font-weight: bold;
    display: block;
    margin: 5px auto 10px;
}

.reb {
    color: #fff;
    text-decoration: none;
    margin-top: 10px;
}
.contain {
    background: rgb(255, 255, 255);
    
}

.left {
    background: #f1f1f1;
    height: 100vh;
    position: fixed;
    width: 300px;
   display: flex;
   flex-direction: column;
   align-items: center;
   
}

.AvatarContain {
    padding-top: 20px;
    text-align: center;
    background: rgba(8, 8, 8, 0.829);
    width: 100%;
}

.avatar {
    height: 50px;
    width: 50px;
    border-radius: 50%;
    border: 1px white solid;
    background: #fff;
}

.Nombre {
    color: #fff;
    margin-top: 5px;
}

.right {
    margin-left: 350px;
    display: grid;
    padding: 50px;
    grid-auto-columns: 300px 300px 300px;
    grid-template-columns: 300px 300px 300px;
    grid-gap: 20px;
}

.upContain {
    margin-top: 200px;
}

.upload {
    display: block;
    margin: 5px auto;
    background: rgba(8, 8, 8, 0.829);
    border: 1px solid #2f2f2f;
    border-radius: 8px;
    height: 40px;
    width: 100px;
    font-size: 18px;
    color: #fff;
}

.salir {
    position: absolute;
    bottom: 0;
    left: 0;
    margin: 10px;
    text-decoration:none;
    color: #ad0000;
    font-weight: bold;
}

.modalcontain {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #000000bd;
    z-index: 99999999999;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modalwrap {
    background: #f1f1f1;
    border-radius: 8px;
    padding: 10px;
    text-align: center;
    position: relative;
    z-index: 3000000;
}
.edit {
    background: #fff;
    width: 90px;
    height: 40px;
    margin: 2.5px;
    border-radius: 8px;
    border: #2f2f2f 1px solid;
}

.closemoda {
    position: absolute;
    top: 0;
    right: 0;
    margin: 10px;
    font-weight: bold;
    color: red;
    background: none;
    border: none;
    font-size: 18px;
    z-index: 3000000000;
}
.CardContain {
    background: #f1f1f1;
    width: 300px;
    border-radius: 8px;
    position: relative;
    z-index: 0;
    border: 1px solid #2f2f2f;
}

.CardContain:hover {
    -webkit-transition: 1s;
    transition: 1s;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    z-index: 10;
    box-shadow: #2f2f2f 0px 0px 15px;
}

.info {
    padding: 5px 10px;
}

.modelo{
    margin: 2px;
}

.Dueño {
    margin: 2px;
}
